.App {
  text-align: center;
  padding: 30px;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.chart-container {
  display: flex;          
  flex-direction: row;  
  flex-wrap: wrap; 
  justify-content: space-between; 
  align-items: flex-start; 
  gap: 20px;             
}

.chart {
  flex: 1;                
  max-width: 45%;         
}